import React, {useRef} from 'react';

const VideoModal = (props) => {
    const vidRef = useRef(null);
    function clickX(){
    
        props.setModal(false);
        vidRef.current.pause();

    }

    return (
        <div className="videomodal"  style={{
            display: props.modal ? "block": "none",
        
          }}   >
              
 
 <video  ref={vidRef} className="vidPlayer" controls src={props.finalLink} type="video/mp4" width="1620" height="1620"/>
<span className="close" onClick={clickX}>X</span>
        </div>
    )
}

export default VideoModal
