import React, { useState, useEffect, useContext } from 'react'
import axios from 'axios' ;

import SampleData from './SampleData.js';
import Message from './Message'; 
import { UserContext } from './UserContext';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import { ProgressBar } from './ProgressBar';


 const FileUpload = (props) => {
  const[file, setFile] = useState(null);
  const [fileName, setFilename] = useState(null);
  const [uploadedFile ,setUploadedFile] = useState({});
  const [message ,setMessage] = useState('message');
  const [treePath, setTreePath] = useState('treePath');
  const [tree, setTree] = useState(false);
  const [ techName, setTechName] = useState('techName');
  const [ techLink, setTechLink] = useState('techLink'); 
  const [ error, setError] = useState(null); 
  const [ submitClk, setSubmitClk] = useState(false);
  const [ uploadModal, setUploadModal] = useState(false);
  const {loggedIn, setLoggedIn}  = useContext(UserContext);
  const types = ['video/mp4', 'video/avi']

  const onChange = e => {
    if(e.target.files && types.includes(e.target.files[0].type)){
    e.preventDefault();
    e.stopPropagation();
    
    setFile(e.target.files[0]);
    setFilename(e.target.files[0].name);
    setError(null)
  }


    else if ( e.target.files === undefined) {
      console.log('undefined')
      setFile(null);
      setFilename(null)
      setError('File Undefined')
    }
    else {
      console.log('Undefined Name or Wrong File')
      setFile(null);
      setFilename(null)
      setError('Undefined Name or Wrong File')
    }
  };



  const fileChange = e => {
    setFilename(e.target.innerHTML)
  }
  
  const onSubmit = async e => {
    e.preventDefault();
    e.stopPropagation();
    const formData = new FormData();
    formData.append('file', file);
    setTree(true)
  
    try{
      const res = await axios.post('http://localhost:5000/upload', formData, {   
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });

      const { fileName, filePath } = res.data;
  
      setUploadedFile( { fileName, filePath})
      setMessage('File Uploaded');
      setTechLink(uploadedFile.filePath)
      setSubmitClk(true);
     
    } catch (err) {
      if (err === 500) {
        setMessage('There was an issue with the server')
      } else {
        setMessage('File Submitted')
      }
    }
  }
  useEffect(() => {
    setUploadedFile('')
    setTechLink('')
         

  }, [submitClk]); 




  return (
    <>
    <div className="Upload" style={uploadModal ? {display:'block'} :{display:'none'}}>

      <form className="uploadform">
        
        <div className='search'>
          <input type='file' className='custom-file-input' id='customFile' 
          onChange={onChange}
          />
          <label  className='custom-file-label' htmlFor='customFile' onChange={fileChange}>
          
          </label>
          <input  className="formBut" type="button" value="Upload"  onClick={onSubmit} />
          { error && <h6 className="fileerror"> {error}</h6> }   
          { file && <ProgressBar file={file} setFile={setFile}/> }
        </div>

        </form>    
            
        <div className="techniqueupload">
       
        <form className="drop" >
            <select className= "select" id="cars" name="cars" onChange={e => setTreePath(e.currentTarget.value)}> 
              <option value="Stand">Standing</option>
              <option value="Closed Guard">Closed Guard</option>
              <option value="Open Guard">Open Guard</option>
              <option value="Passing">Passing</option>
              <option value="Top">Top</option>
              <option value="Back">Back</option>      
            </select>
            <input type="text" htmlFor = "newValue" placeholder = "Label your video" value = {submitClk ? '' : techName} onChange={e => setTechName(e.currentTarget.value)}             
          /><span className="techname">Technique name</span><span className="positionname">Position</span>
        
       </form>
     
         
    
     {message ? <Message msg={message} /> :null}



     </div>
     <SampleData treePath={treePath} submitClk={submitClk}
      setSubmitClk={setSubmitClk} tree={tree} setTree={setTree}
      techName={techName} fileName={fileName} techLink={techLink} />

        </div>
        <AddCircleIcon style={loggedIn ? {display:'block'} :{display:'none'}} className ="addBut"  onClick={() => setUploadModal(true)}/>
             </>
  )
}

export default FileUpload