import React , {useContext} from 'react'
import { UserContext } from './UserContext';
import Techniques from './TechniqueList';
import useFirestore from './useFirestore.js';

const Video = () => { 
   const { docs } = useFirestore('videos');
   const {loggedIn, setLoggedIn}  = useContext(UserContext);
 
   console.log(docs);

return (
    <div className="Main" style={loggedIn ? {display:'block'} :{display:'none'}}>
         { docs && docs.map(doc => (
      <video className="three" controls key={doc.id} type="video/mp4" src={doc.url} alt="Video" > </video>

      ))}
      <h3 className="vdb">Your Gym's Virtual Online Database</h3>
      
      <video className="three" controls   src={'http://localhost:5000/rh.mp4'} type="video/mp4" />
      <video className="three" controls  src={'http://localhost:5000/mg.mp4'} type="video/mp4" />
      <video className="three" controls  src={'http://localhost:5000/mm.mp4'} type="video/mp4" />
  
      <div className = "r1Contain">
      <video className="three" controls  src={'http://localhost:5000/jt.mp4'} type="video/mp4" />
      <video className="three" controls  src={'http://localhost:5000/ll.mp4'} type="video/mp4" />
      <video className="three" controls  src={'http://localhost:5000/rl.mp4'} type="video/mp4" />
    

      </div>

</div>

)}

export default Video
