import React, { useEffect, useState } from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";

function Techniques(props) {
const [list, setList] = React.useState( [
		{
			closedguard: [
				{
					name: "Armbar",
					link: "",
					x: "X",
					id: "1",
				},

				{
					name: "Triangle",
					link: "",
					x: "X",
					id: "2",
				},

				{
					name: "Omaplata",
					link: "",
					x: "X",
					id: "3",
				},
        ],
        openguard: [
          {
            name: "Armbar",
            link: "",
            x: "X",
            id: "1",
          },
  
          {
            name: "Triangle",
            link: "",
            x: "X",
            id: "2",
          },
  
          {
            name: "Omaplata",
            link: "",
            x: "X",
            id: "3",
          },
          ]
      }  
     ]
    )


	const [subMenuState, setSubMenuState] = useState([false, false]);
  const [modalLink, setModalLink]= useState('')
  const [modal, setModal]= useState(false)
 
  if( props.submitClk === true) {
    setList(prevState => [...prevState, "Hi Buddy"]);
  }

	const toggleSubMenu = (e, i) => {
		console.log('hey')
		e.preventDefault();
		// Clone the array
		const newState = subMenuState.slice(0);
		// Toggle the state of the clicked sub-menu
		newState[i] = !newState[i];
		// Set the new state
		setSubMenuState(newState);
	};



  const handleRemove = (id) => {
    const newList = list[0].closedguard.filter(move => move.id !== id);
    setList([{ closedguard: newList }]);
};



  useEffect(()=>{
    console.log(list)},
    [list]);



	return (
		<div className="treeView">
			<span onClick={(e) => toggleSubMenu(e, 0)}>
				<AddCircleIcon style={{ position: "relative", top: "26" }} />
			</span>{" "}
			<ul className="techUl">
				{" "}
				Closed Guard
				style={subMenuState[0] ? { display: "block" } : { display: "none" }}
				{list.length > 0 &&
					list.map((pos) =>
						pos.closedguard.map((move, index) => (
							<li
								onClick={() => handleRemove(move.id)}
								key={move.id}
								className="techLi"
							>
								{move.name}
								<span> {move.x}</span>
							</li>
						))
					)}
			</ul>
			<span onClick={(e) => toggleSubMenu(e, 1)}>
				<AddCircleIcon style={{ position: "relative", top: "26" }} />
			</span>{" "}
			<ul className="techUl">
				{" "}
				Open Guard
			
				{list &&
					list.map((pos) =>
						pos.openguard.map((move, index) => (
							<li
								onClick={() => handleRemove(move.id)}
								key={move.id}
								className="techLi"
							>
								{move.name}
								<span> {move.x}</span>
							</li>
						))
					)}
			</ul>
		</div>
	);
}
export default Techniques





