import React, { useEffect, useState } from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";

import VideoModal from './VideoModal'

function SampleData(props ) {
	console.log(props)
	const [ tech, setTech ] = useState ( [
				
			{
				name: "Armbar",
				link: "",
				position: "Closed Guard",
				x: "X",
				id: "1",
			},

			{
				name: "Triangle",
				link: "",
				position: "Closed Guard",
				x: "X",
				id: "2",
			},

			{
				name: "Omaplata",
				link: "",
				position: "Closed Guard",
				x: "X",
				id: "3",
			},
  
 
			{
				name: "Berimbolo",
				link: "",
				position: "Open Guard",
				x: "X",
				id: "4",
			},

			{
				name: "Reverse DLR",
				link: "",
				position: "Open Guard",
				x: "X",
				id: "5",
			},

			{
				name: "Collar Sleeve Omaplata",
				link: "",
				position: "Open Guard",
				x: "X",
				id: "6",
			},
		
			{
				name: "Knee Cut",
				link: "",
				position: "Passing",
				x: "X",
				id: "7",
			},
			{
				name: "North/South Choke",
				link: "",
				position: "Top",
				x: "X",
				id: "8",
			},
			{
				name: "Bow and Arrow Choke",
				link: "",
				position: "Back",
				x: "X",
				id: "9",
			},
			{
				name: "Double Leg",
				link: "",
				position: "Standing",
				x: "X",
				id: "10",
			}

 
			
	])


	let link = JSON.stringify(props.fileName );  
	console.log(link)
	let link2 = link.slice(1,-1);
	let vidLink = 'http://localhost:5000';
	let finLink = vidLink.concat('/', link2)

	console.log(props.fileName)
    const [subMenuState, setSubMenuState] = useState([ false, false]);
	
	const [finalLink, setFinalLink] = useState('');
	const sub = props.submitClk;
    const closedGuard = tech.filter(tec => tec.position === 'Closed Guard');
	const openGuard = tech.filter(tec => tec.position === 'Open Guard');
	const standing = tech.filter(tec => tec.position === 'Standing');
	const passing = tech.filter(tec => tec.position === 'Passing');
	const top = tech.filter(tec => tec.position === 'Top');
	const back = tech.filter(tec => tec.position === "Back");

	const [modal, setModal]= useState(false)
		

	useEffect(() => {
		const json = localStorage.getItem("tech");
		const techs = JSON.parse(json);
		if (techs) {
		  setTech(techs);
		}
	  }, []);


	  useEffect(() => {
		link = JSON.stringify(props.fileName )
		link2 = link.slice(1,-1)		
		finLink = vidLink.concat('/', props.fileName);
		setFinalLink(finLink)
		setTech( [...tech , {name:`${props.techName}`, link: `${finLink}` , position:`${props.treePath}`, x:"X",id : Math.random().toString(36).substr(2, 8)} ]);
		props.setSubmitClk(false)		
		console.log('weeeeeeee');
		const json = JSON.stringify(tech);
		localStorage.setItem("tech", json);
	

	  }, [props.tree])

	// const doJob =  () => {	
	// 	link = JSON.stringify(props.fileName )
	// 	link2 = link.slice(1,-1)		
	// 	finLink = vidLink.concat('/', props.fileName);
	// 	setFinalLink(finLink)
	// 	setTech( [...tech , {name:`${props.techName}`, link: `${finLink}` , position:`${props.treePath}`, x:"X",id : Math.random().toString(36).substr(2, 8)} ]);
	// 	props.setSubmitClk(false)		
	// 	console.log(finLink);
	// 	const json = JSON.stringify(tech);
	// 	localStorage.setItem("tech", json);
			
	// 		}


 
	const toggleSubMenu = (e, i) => {
		e.preventDefault();
		const newState = subMenuState.slice(0);
		newState[i] = !newState[i];
		setSubMenuState(newState);
		};



	const handleRemove = (id) => {
		const newList = tech.filter(pos => id !== pos.id);
		setTech( newList );
	};


	//<button onClick={doJob} >Update List</button>


	return (
		<div className="guard">
		

			<ul className="techUl">Closed Guard	<span onClick={(e) => toggleSubMenu(e, 0)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ closedGuard.map(pos => (
			
			<li href={finalLink} key={pos.id} className="techLi" style={
				subMenuState[0] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)}>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>


			<ul className="techUl"> Open Guard	<span onClick={(e) => toggleSubMenu(e, 1)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ openGuard.map(pos => (
			
			<li href={finalLink} className="techLi" style={
				subMenuState[1] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)}	>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>


			<ul className="techUl"> Standing	<span onClick={(e) => toggleSubMenu(e, 2)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ standing.map(pos => (
			
			<li href={finalLink} key={pos.id} className="techLi" style={
				subMenuState[2] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)}	>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>

			<ul className="techUl"> Top	<span onClick={(e) => toggleSubMenu(e, 3)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ top.map(pos => (
			
			<li href={finalLink} key={pos.id} className="techLi" style={
				subMenuState[3] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)}	>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>

			<ul className="techUl"> Passing	<span onClick={(e) => toggleSubMenu(e, 4)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ passing.map(pos => (
			
			<li href={finalLink} key={pos.id} className="techLi" style={
				subMenuState[4] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)}	>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>

			<ul className="techUl"> Back Control	<span  onClick={(e) => toggleSubMenu(e, 5)}>
				<AddCircleIcon style={{ position: "relative", bottom: "24",right:'40px' }} />
			</span>{" "}
			{ back.map(pos => (
			
			<li href={finalLink} key={pos.id} className="techLi" style={
				subMenuState[5] ? { display: "block" } : { display: "none" }}
				onClick={ () => setModal(true)} 	>{pos.name} <span style={{padding:"12px", color:'red',fontSize:'6px'}} onClick={(id) => handleRemove(pos.id)}>{pos.x}</span>
			</li>
				))}
			</ul>
 
			<VideoModal finalLink={finalLink} modal={modal} setModal={setModal}/>
		</div>
	);
}

export default SampleData;
