import React, {useState, useContext} from 'react';
import LaptopIcon from '@material-ui/icons/Laptop';
import EmailIcon from '@material-ui/icons/Email';
import Login from './Login'
import loginUser from './Login'
import { UserContext } from './UserContext';
const Navbar = () => {
    const [loginClick ,setLoginClick] = useState('false');
   const {loggedIn, setLoggedIn}  = useContext(UserContext);
    

    function handleLogin (props) {
        setLoginClick(prevLogin => !prevLogin);
    }
    
let user = JSON.stringify(loginUser)
    return (
        <div className="navbar">
            <span> 
            
            <ul className="toplinks">
              <h3 className="company">REEL / ROLL</h3>
                <li>Home</li>
                <li>Services</li>
                <li>Demo</li>
                <EmailIcon/>
                <LaptopIcon onClick={() => handleLogin()} /> 
              {loggedIn  ?  <li> {user} You are logged in </li>: <li>Please Sign In </li>}

              <Login style={{color: 'red'} }loginUser={loginUser}
            submitLogin= {(e) => setLoggedIn(true)}
            loginClick={loginClick} 
            loggedIn={loggedIn} />
            </ul>
            
            </span>
        
        </div>
    )
}

export default Navbar