import React, {useState} from 'react'
import PersonPinIcon from '@material-ui/icons/PersonPin';


 const Login = (props) => {
    const [loginUser ,setLoginUser] = useState('');
    const [loginPassword, setLoginPassword] = useState('');


    console.log(loginUser)
    return (
     <div className= "TopLoginContainer"style={ props.loggedIn ? { display:'none'} : {display : 'flex'}} >
        <div className='LoginContainer'  style={ props.loginClick ? { display:'none'} : {display : 'flex'}} >
            <ul className="loginNav">
            <form className="login"  >
                <PersonPinIcon className= "personicon" />
               
                <input className ="logtext" type="text"   placeholder="JiuJitsu" onChange={(e) => setLoginUser(e.target.value)}/>
            
                <input className ="logpass"  type="password"  value="Roll01234" />
                <br/>  
                <input className ="logsub" type="submit"  value="Login" onClick={function tha(e) {e.preventDefault(); props.submitLogin()}}/>
             </form>
            </ul>
        </div>
     </div>
    )   
}

export default Login